import React, { useState, useEffect } from 'react';
import { Button, message, Row, Col, Card, List, Modal } from 'antd';
import { fetchMTGStockData, fetchScryfallData } from './api';

const Optimize = () => {
  const [cards, setCards] = useState([]);
  const [sites, setSites] = useState([]);
  const [selectedCard, setSelectedCard] = useState(null);
  const [mtgStockData, setMtgStockData] = useState(null);
  const [scryfallData, setScryfallData] = useState(null);
  const [isModalVisible, setIsModalVisible] = useState(false);

  useEffect(() => {
    fetch('/api/cards')
      .then(response => response.json())
      .then(data => setCards(data))
      .catch(error => console.error('Error fetching cards:', error));

    fetch('/api/sites')
      .then(response => response.json())
      .then(data => setSites(data))
      .catch(error => console.error('Error fetching sites:', error));
  }, []);

  const handleOptimize = async () => {
    try {
      const response = await fetch('/api/optimize', { method: 'POST' });
      const result = await response.json();
      message.success('Optimization completed successfully!');
      // Handle displaying results or storing them in state
    } catch (error) {
      message.error('Optimization failed!');
      console.error('Error during optimization:', error);
    }
  };

  const handleCardClick = async (card) => {
    setSelectedCard(card);
    try {
      const mtgStockData = await fetchMTGStockData(card.card);
      const scryfallData = await fetchScryfallData(card.card);
      setMtgStockData(mtgStockData);
      setScryfallData(scryfallData);
      setIsModalVisible(true);
    } catch (error) {
      message.error('Error fetching card data');
    }
  };

  const handleModalClose = () => {
    setIsModalVisible(false);
    setMtgStockData(null);
    setScryfallData(null);
  };

  return (
    <div>
      <h1>Optimize</h1>
      <Button type="primary" onClick={handleOptimize}>Run Optimization</Button>
      <Row gutter={16}>
        <Col span={12}>
          <Card title="MTG Card List">
            <List
              bordered
              dataSource={cards}
              renderItem={card => (
                <List.Item onClick={() => handleCardClick(card)}>
                  {card.card}
                </List.Item>
              )}
            />
          </Card>
        </Col>
        <Col span={12}>
          <Card title="Site List">
            <List
              bordered
              dataSource={sites}
              renderItem={site => <List.Item>{site.name}</List.Item>}
            />
          </Card>
        </Col>
      </Row>
      <Modal
        title={selectedCard ? selectedCard.card : ''}
        visible={isModalVisible}
        onCancel={handleModalClose}
        footer={[
          <Button key="close" onClick={handleModalClose}>
            Close
          </Button>
        ]}
      >
        {mtgStockData && (
          <div>
            <h2>MTG Stock Data</h2>
            <pre>{JSON.stringify(mtgStockData, null, 2)}</pre>
          </div>
        )}
        {scryfallData && (
          <div>
            <h2>Scryfall Data</h2>
            <pre>{JSON.stringify(scryfallData, null, 2)}</pre>
          </div>
        )}
      </Modal>
    </div>
  );
};

export default Optimize;
